nav {
    display: flex;
    justify-content: center;
    background-color: rgba(240, 240, 240, 0.4);
    position: -webkit-sticky;
    position: sticky;
    z-index: 100;
    top: -1px;
    transition: 350ms;
}

nav:hover {
    background-color: rgba(240, 240, 240, 0.8);
}

.nav-item {
    text-decoration: none;
    margin: 15px;
    font-family: Sans-serif;
    transition: 400ms;
    color: #474747;
    padding: 5px 10px 5px 10px;
    border-radius: 5px
}

.nav-item:hover {
    color: #000000;
    -webkit-transform: translateY(6px);
            transform: translateY(6px);
}

.header {
    font-size: 100px;
    font-family: Trebuchet MS;
    text-align: center;
    color: rgb(20,20,20);
    margin-bottom: 10px;
}

#quote {
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    color: rgb(20,20,20);
    padding-bottom: 20px;
    font-size: 23px;
}
html {
    scroll-behavior: smooth;
}

body {
    margin: 0%;
    font-family: Quicksand;
}

body:after {
    content: "";
    background: url("https://www.timeforkids.com/wp-content/uploads/2019/09/final-cover-forest.jpg?w=1024");
    background-attachment: fixed;
    overflow-x: hidden;
    background-position-x: center;
    background-size: 100%;
    opacity: 1;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;   
}

p, h1, h2, h3 {
    color: white;
}

p {
    font-family: Quicksand, sans-serif, Arial, Helvetica;
    font-size: 20px;
}

.content {
    background-color: black;
    margin-bottom: 0px;
    padding: 15px;
}
.abt-card-cont {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: -15px;
}

.ab-sect-card {
    display: grid;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid white;
}


.pic-item {
    grid-column: 2;
    grid-row: 1 / span 2;
    overflow: hidden;
    width: 400px;
    transition: 0.4s ease;
    border-radius: 5%;
}

.ab-sect-card .ab-sect-cont {
    grid-column: 1;
    grid-row: 2;
}

.ab-sect-card .interest {
    grid-column: 1;
    grid-row: 1;
}

.gray .pic-item {
    grid-column: 1;
    grid-row: 1 / span 2;
    overflow: hidden;
    width: 400px;
    transition: 0.4s ease;
    border-radius: 5%;
}

.gray .ab-sect-cont {
    grid-column: 2;
    grid-row: 2;
}

.gray .interest {
    grid-column: 2;
    grid-row: 1;
}

.interest {
    font-size: 66px;
    text-align: center;
    font-family: sans-serif, Helvetica, Arial;
    padding-bottom: 5px;
    padding-top: 20px;
    width: 230px;
    border-bottom: 3px solid white;
}

.ab-sect-cont {
    width: 370px;
    padding: 24px;
    padding-bottom: 28px;
}

.pic-item img {
    transition: 0.4s ease;
}

.pic-item img:hover {
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
}

.pic-item:hover {
    border-radius: 15%;
}

.pic {
    height: 100%;
    width: 100%;
}

.grid-item {
    text-align: center;
    margin: auto;
}

.ab-header {
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 0px;
    font-size: 65px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    border-bottom: 1px Solid White;
    width: 900px;
    padding-top: 60px;
    padding-bottom: 40px;
}

.container {
    overflow: hidden;
}
